import React, { FC } from 'react'
import { UnityApp } from '../pages/unity-app'
import { Route, Switch } from 'react-router-dom'
import { addUrlPath } from '../functions/addUrlPath'
import { Dashboard } from '../pages/dashboard'
import { Menu } from '../components/Menu'
import { Layout } from 'antd'
import { Topbar } from '../components/Topbar'
import { UserInfo } from '../components/UserInfo'
import * as queryString from 'querystring'

import './App.less'

const { Header, Sider, Content } = Layout

export const App: FC = () => {
  const params = queryString.parse(document.location.search)
  const redirect = params['?redirect']

  if (redirect && redirect.length) {
    window.location.pathname = addUrlPath(
      `/${redirect instanceof Array ? redirect[0] : redirect}/`
    )
    window.location.search = ''
  }

  return (
    <div className={'App'}>
      <Layout
        style={{
          width: '100%',
          height: '100%',
        }}
      >
        <Header
          style={{
            backgroundColor: '#ffffff',
          }}
        >
          <Topbar />
        </Header>
        <Layout>
          <Sider
            style={{
              backgroundColor: '#F0F0F0',
            }}
            width={256}
            theme={'light'}
          >
            <UserInfo />
            <Menu />
          </Sider>
          <Content>
            <Switch>
              <Route exact path={addUrlPath('/')} component={Dashboard} />
              <Route path={addUrlPath('/unity')} component={UnityApp} />
            </Switch>
          </Content>
        </Layout>
      </Layout>
    </div>
  )
}
